.nav {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 1300px;
  height: 100px;
  margin: 0 auto;

  @media (max-width: @desktop-width) {
    width: 1060px;
    background-color: red;
  }

  @media (max-width: @tablet-width) {
    width: 700px;
    background-color: gold;
  }

  @media (max-width: @mobile-width) {
    width: 328px;
    background-color: green;
  }
}
.header-arrow {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 80px;
  height: 28px;
  position: absolute;
  z-index: 2;
  right: -104px;
  bottom: -395px;
  font-family: "Satoshi-Regular", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 140%;
  transform: rotate(90deg);

  &:hover {

    animation-name: header-arrow;
    animation-duration: .75s;
    animation-timing-function: linear;
    animation-iteration-count: 1;
    animation-direction: normal;
    animation-fill-mode: forwards;
  
  
    @keyframes header-arrow{
      0%{
          width: 80px;
      }

      25%{
          width: 95px;
            right: -111.5px;
            bottom: -403px;
      }

      75%{
          width: 95px;
              bottom: -403px;
              right: -111.5px;
      }
    
      100%{
          width: 80px;
      }
    }
  }



  @media (max-width: @desktop-width) {
    width: 70px;
    font-size: 16px;
    line-height: 140%;
    position: absolute;
    right: -75px;
    bottom: -320px;

    &:hover {

      animation-name: header-arrow-desc;
      animation-duration: .73s;
      animation-timing-function: linear;
      animation-iteration-count: 1;
      animation-direction: normal;
      animation-fill-mode: forwards;

      @keyframes header-arrow-desc{
        0%{
            width: 70px;
        }
  
        25%{
            width: 85px;
              right: -82.5px;
              bottom: -328px;
        }
  
        75%{
            width: 85px;
                bottom: -328px;
                right: -82.5px;
        }
      
        100%{
            width: 70px;
        }
      }
    }


  } 

  @media (max-width: @tablet-width) {
    display: none;
  }
}
.header-arrow-svg {
  display: block;
  transform: rotate(90deg);
}
.nav-list {
  position: absolute;
  right: 70px;
  top: 39px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 665px;

  @media (max-width: @desktop-width) {
    width: 550px;
    right: 65px;
    top: 40px;
  }  

  @media (max-width: @tablet-width) {
    display: none;
  }

  // @media (max-width: @mobile-width) {
  //   width: 328px;
  //   height: auto;
  //   background-color: green;
  // }
}
.nav-item a {
    display: block;
  font-family: "Satoshi-Medium", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 24px;
  text-transform: uppercase;
  transition: 1s;
  position: relative;

  &:after {
    display: block;
    position: absolute;
    left: 0; /*изменить на right:0;, чтобы изменить направление подчёркивания */
    width: 0;/*задаём длинну линии до наведения курсора*/
    height: 2px; /*задаём ширину линии*/
    background-color:#28C0E2; /*задаём цвет линии*/
    content: "";
    transition: width 0.3s ease-out; /*задаём время анимации*/
  }

  &:hover:after,
  &:focus:after {
	width: 100%; /*устанавливаем значение 100% чтобы ссылка подчёркивалась полностью*/
  }
  &:hover {
    color:#28C0E2;
  }



  
  @media (max-width: @desktop-width) {
    font-size: 14px;
    line-height: 19px;
  }

  @media (max-width: @tablet-width) {
    width: 149px;
    margin-top: 40px;
    margin-left: 335px;
    font-family: "Satoshi-Medium", sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 32px;
    color: @background_page;
    text-align: left;

    &:after {
      display: none;
    }
  
    &:hover:after,
    &:focus:after {
      display: none;
    }

  }

  @media (max-width: @mobile-width) {
    margin-top: 20px;
    margin-left: 250px;
    margin-bottom: 25px;
    // width: 57px;
    height: auto;
    font-size: 16px;
    line-height: 140%;
  }
}


