.nav-opened {
  position: absolute;
  z-index: 1;
  top: 0;
  right: -332px;
  display: block;
  width: 1364px;
  height: 100vh;
  background-color: @basic_dark;
  overflow: hidden;

  animation-name: menu;
  animation-duration: .5s;
  animation-timing-function: linear;
  animation-iteration-count: 1;
  animation-direction: normal;
  animation-fill-mode: forwards;


  @keyframes menu{
    0%{
      right: -1080px;
    }
  
    100%{
      right: -333px;
    }
  }

  @media (max-width: @mobile-width) {
    position: absolute;
    z-index: 1;
    top: 0;
    right: -220px;
    display: block;
    width: 800px;
    height: 100vh;
    background-color: @basic_dark;

    animation-name: menu;
    animation-duration: .5s;
    animation-timing-function: linear;
    animation-iteration-count: 1;
    animation-direction: normal;
    animation-fill-mode: forwards;
  
  
    @keyframes menu{
      0%{
        right: -1080px;
      }
    
      100%{
        right: -233px;
      }
    }
  }
}

