.nav__toggle-head {
  display: none;


  @media (max-width: @tablet-width) {
    display: block;
    content: "";
    position: absolute;
    z-index: 5;
    top: 48px;
    right: 37px;
    width: 30px;
    height: 2px;
    background-color: #FDFDFD;
    transform: rotate(45deg);

    &::before {
      content: "";
      position: absolute;
      z-index: 4;
      top: 0;
      right: 0;
      width: 30px;
      height: 2px;
      background-color: #FDFDFD;
      transform: rotate(-90deg);
    }
  }

  @media (max-width: @mobile-width) {
    display: block;
    position: absolute;
    z-index: 5;
    top: 45px;
    right: -2px;
    background-color: #303030;
    width: 30px;
    height: 30px;

    &::after {
    content: "";
    position: absolute;
    z-index: 5;
    top: 0;
    right: 0;
    width: 30px;
    height: 2px;
    background-color: #FDFDFD;
    }

    &::before {
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      width: 30px;
      height: 2px;
      background-color: #FDFDFD;
    }
  }
}
