.privacy {
  width: 1300px;
  margin: 0 auto;

  @media (max-width: @desktop-width) {
    width: 1060px;
  }

  @media (max-width: @tablet-width) {
    width: 700px;
  }

  @media (max-width: @mobile-width) {
    width: 328px;
  }
}
.privacy-title {
  margin: 150px 0 50px;
  font-family: "Satoshi-Bold", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 50px;
  line-height: 68px;

  @media (max-width: @desktop-width) {
    margin: 100px 0 50px;
  }

  @media (max-width: @tablet-width) {
    width: 700px;
    margin: 70px 0 50px;
    font-size: 36px;
    line-height: 49px;
  }

  @media (max-width: @mobile-width) {
    margin: 50px 0;
  }
}
.privacy-text {
  width: 1170px;
  margin-bottom: 15px;
  font-family: "Satoshi-Regular", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 140%;

  @media (max-width: @desktop-width) {
    margin-bottom: 15px;
    width: 969px;
    font-size: 16px;
    line-height: 140%;
  }

  @media (max-width: @tablet-width) {
    width: 610px;
    margin-bottom: 14px;
  }

  @media (max-width: @mobile-width) {
    margin-bottom: 12px;
    width: 328px;
  }
}
.privacy-subtitle {
  margin: 65px 0 30px;
  font-family: "Satoshi-Bold", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 36px;
  line-height: 49px;

  @media (max-width: @desktop-width) {
    margin: 65px 0 30px;
  }

  @media (max-width: @tablet-width) {
    margin: 47px 0 30px;
    font-size: 24px;
    line-height: 32px;
  }

  @media (max-width: @mobile-width) {
    margin: 48px 0 30px;
  }

  @media (max-width: @mobile-width) {

  }
}
.privacy-subtitle-2 {
  margin: 50px 0 30px;

  @media (max-width: @tablet-width) {
    margin: 47px 0 30px;

  }

  @media (max-width: @mobile-width) {
    margin: 48px 0 30px;
  }
}
.privacy-subtitle-3 {
  margin: 48px 0 30px;

  @media (max-width: @desktop-width) {
    margin: 45px 0 30px;
  }

  @media (max-width: @tablet-width) {
    margin: 49px 0 30px;
  }

  @media (max-width: @mobile-width) {
    margin: 47px 0 30px;
  }
}
.privacy-subtitle-4 {
  margin: 50px 0 30px;

  @media (max-width: @desktop-width) {
    margin: 50px 0 30px;
  }
  @media (max-width: @mobile-width) {
    margin: 48px 0 30px;
  }
} 
.privacy-subtitle-5 {
  margin: 47px 0 30px;
  font-family: "Satoshi-Bold", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 36px;
  line-height: 49px;

  @media (max-width: @tablet-width) {
    margin: 47px 0 30px;
    font-size: 24px;
    line-height: 32px;
  }

  @media (max-width: @mobile-width) {

    margin: 46px 0 30px;
  }
}
.privacy-subtitle-6 {
  margin: 50px 0 30px;
  font-family: "Satoshi-Bold", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 36px;
  line-height: 49px;

  @media (max-width: @tablet-width) {
    font-size: 24px;
    line-height: 32px;
    margin: 47px 0 30px;
  }

  @media (max-width: @mobile-width) {
    margin: 45px 0 30px;
  }
}
.privacy-subtitle-7 {
  margin: 50px 0 30px;
  font-family: "Satoshi-Bold", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 36px;
  line-height: 49px;

  @media (max-width: @desktop-width) {
    margin: 45px 0 30px;
  }

  @media (max-width: @tablet-width) {
    
    font-size: 24px;
    line-height: 32px;
  }
}
.privacy-lists {
  display: block;

  // @media (max-width: @desktop-width) {
  //   margin-top: -2px;
  // }
}
.privacy-items {
  margin-left: 30px;
  width: 1170px;
  list-style: disc;
  font-family: "Satoshi-Regular", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 140%;

  @media (max-width: @desktop-width) {

    width: 910px;
    font-size: 16px;
    line-height: 140%;
  }

  @media (max-width: @tablet-width) {
    width: 600px;
  }

  @media (max-width: @mobile-width) {
    width: 290px;
  }
}
.privacy-link {
  display: block;
  width: 100px;
  height: 20px;
  margin: 91px 0 89px -10px;
  text-align: right;
  font-family: "Satoshi-Regular", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 140%;

  @media (max-width: @desktop-width) {
    margin: 64px 0 63px -10px;
    font-size: 16px;
    line-height: 140%;
  }

  @media (max-width: @tablet-width) {
    margin: 39px 0 37px 0;
    width: 90px;
  }

  @media (max-width: @mobile-width) {
    display: flex;
    align-items: center;
    margin: 37px 28px 42px 0;
  }
}
.privacy-link-svg {
  display: block;
  margin-left: 7px;
  margin-top: 2px;

  @media (max-width: @desktop-width) {
    margin-left: 12px;
    margin-top: 2px;
  }

  @media (max-width: @tablet-width) {
    margin: 0;
    margin-left: 5px;
    margin-right: 0;
  }

  @media (max-width: @mobile-width) {
    margin-right: 0;
    margin-top: 0;
    margin-left: 4px;
  }
}
.privacy-link-text {
  display: block;
  margin-top: -22px;
  margin-left: 7px;

  @media (max-width: @desktop-width) {
    margin-top: -20px;
    margin-left: 7px;
  }

  @media (max-width: @tablet-width) {
    margin-top: -20px;
    margin-left: -10px;
  }

  @media (max-width: @mobile-width) {
    margin-top: 0px;
    margin-left: 9px;
  }

}