.footer-wrapper {
  position: relative;
  margin-top: 200px;
  background-color: @basic_dark;

  &::before {
    content: "";
    position: absolute;
    bottom: 200px;
    right: 0;
    width: 100%;
    height: 1px;
    background-color: @basic-grey;

    @media (max-width: @desktop-width) {
      bottom: 150px;
    }

    @media (max-width: @mobile-width) {
      bottom: 341px;
    }
  }

  @media (max-width: @desktop-width) {
    margin-top: 155px;
  }

  @media (max-width: @tablet-width) {
    margin-top: 100px;
  }

  @media (max-width: @mobile-width) {
    margin-top: 72px;
  }
}
.footer-wrp {
  width: 1300px;
  height: 966px;
  margin: 0 auto;
  padding-top: 100px;

  @media (max-width: @desktop-width) {
    width: 1060px;
    height: 910px;
  }

  @media (max-width: @tablet-width) {
    width: 700px;
  }

  @media (max-width: @mobile-width) {
    width: 328px;
    height: 1450px;
    padding-top: 70px;
  }
}
.footer-inf {
  display: flex;
  justify-content: space-between;
  margin-bottom: 70px;
  width: 1300px;
  height: 136px;

  @media (max-width: @desktop-width) {
    width: 1060px;
    height: 136px;
  }

  @media (max-width: @tablet-width) {
    width: 700px;
    height: 147px;
  }

  @media (max-width: @mobile-width) {
    display: block;
    width: 328px;
    height: 287px;
    margin-bottom: 43px;
  }
}
.footer-title {
  font-family: "Satoshi-Bold", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 50px;
  line-height: 68px;
  color: @background_page;

  @media (max-width: @tablet-width) {
    width: 340px;
    font-size: 36px;
    line-height: 49px;

  }

  @media (max-width: @mobile-width) {
    display: block;
    width: 328px;
  }
}
.footer-text {
  width: 527px;
  height: 112px;
  margin-top: 18px;
  margin-right: 7px;
  font-family: "Satoshi-Regular", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 140%;
  color: @background_page;

  @media (max-width: @desktop-width) {
    width: 420px;
    height: 88px;
    font-size: 16px;
    line-height: 140%;
  }

  @media (max-width: @tablet-width) {
    width: 340px;
    height: 110px;
    font-size: 16px;
    line-height: 140%;
    margin: 0;
    margin-top: 12px;
  }

  @media (max-width: @mobile-width) {
    width: 300px;
    height: 110px;
    margin-top: 30px;
  }
}
.form {
  display: flex;
  position: relative;
  margin-top: -5px;

  @media (max-width: @tablet-width) {
    margin-top: -16px;
  }

  @media (max-width: @mobile-width) {
    display: block;
    margin-top: 0;
  }
}
.form-wrapper {
  width: 770px;
  margin-top: -3px;

  @media (max-width: @desktop-width) {
    width: 630px;
  }

  @media (max-width: @tablet-width) {
    width: 360px;
  }

  // // @media (max-width: @mobile-width) {
  // //   width: 328px;
  // //   background-color: green;
  // // }
}
.form-text {
  width: 530px;
  margin-top: -3px;

  @media (max-width: @desktop-width) {
    width: 430px;
  }

  @media (max-width: @tablet-width) {
    width: 340px;
  }

  // // @media (max-width: @mobile-width) {
  // //   width: 328px;
  // //   background-color: green;
  // // }
}
.form-name,
.form-email, 
.form-country,
.form-interest,
.form-message {
  font-family: "Satoshi-Regular", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 140%;
  color: @basic-grey;
  width: 640px;
  height: 50px;
  border: none;
  border-bottom: 1px solid @background_page;
  background-color: @basic_dark;
  margin-bottom: 40px;

  @media (max-width: @desktop-width) {
    font-size: 16px;
    line-height: 140%;
    width: 520px;
    margin-bottom: 38px;
  }

  @media (max-width: @tablet-width) {
    width: 340px;
  }

  @media (max-width: @mobile-width) {
    width: 328px;
  }
}
.form-name::placeholder,
.form-email::placeholder,
.form-message::placeholder {
  color: @basic-grey;
}
.form-email {
  height: 50px;
  margin-bottom: 40px;

  @media (max-width: @desktop-width) {

    margin-bottom: 38px;
  }
}
.form-country {
  height: 50px;
  margin-bottom: 40px;

  @media (max-width: @desktop-width) {

    margin-bottom: 38px;
  }
}
.form-message {
  width: 529px;
  border: none;

  @media (max-width: @desktop-width) {
    width: 430px;
  }

  @media (max-width: @tablet-width) {
    width: 340px;
  }

  @media (max-width: @mobile-width) {
    margin-top: 4px;
    width: 328px;
  }
}

.form-file {
  display: block;
  margin-top: 10px;
  font-family: "Satoshi-Regular", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 140%;
  width: 529px;
  height: 40px;
  cursor: pointer;
  border-bottom: 1px solid @background_page;
  background-color: @basic_dark;
  color: @basic-blue;

  @media (max-width: @desktop-width) {
    font-size: 16px;
    line-height: 140%;
    width: 430px;
  }

  @media (max-width: @tablet-width) {
    width: 340px;
  }

  @media (max-width: @mobile-width) {
    width: 328px;
    margin-top: 13px;
  }
}

.form-button {
  position: absolute;
  right: 0;
  bottom: 45px;
  width: 120px;
  height: 120px;
  border: none;
  border-radius: 50%;
  background-color: @basic-blue;
  font-family: "Satoshi-Regular", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 140%;
  transition: 1s;

  &:hover {
    right: -15px;
    bottom: 30px;
    width: 150px;
    height: 150px;
  }

  &:active {
    right: 0;
    bottom: 45px;
    width: 120px;
    height: 120px;
  }


  @media (max-width: @desktop-width) {
    bottom: 45px;
  }

  @media (max-width: @mobile-width) {
    position: relative;
    margin-top: 83px;
    margin-left: 208px;
  }
}
.social {
  position: relative;
  margin-top: 110px;
  width: 1300px;
  height: 200px;
  display: flex;
  align-items: center;
  justify-content: space-between;



  @media (max-width: @desktop-width) {
    width: 1060px;
    height: 150px;
  }

  @media (max-width: @tablet-width) {
    width: 700px;
    height: 150px;
  }

  @media (max-width: @mobile-width) {
    display: block;
    width: 328px;
    height: 340px;
    margin-top: 65px;
    margin-left: 8px;
  }
}
.social-title {
  font-family: "Satoshi-Regular", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 140%;
    color: @background_page;

  @media (max-width: @desktop-width) {
    font-size: 16px;
    line-height: 140%;
  }

  @media (max-width: @mobile-width) {
    margin-top: 50px;
    margin-bottom: 10px;
  }
  
}
.social-list {
  display: flex;
  justify-content: space-between;
  width: 972px;

  @media (max-width: @desktop-width) {
    margin-top: 5px;
    width: 788px;
  }

  @media (max-width: @tablet-width) {
    width: 427px;
  }

  @media (max-width: @mobile-width) {
    display: block;
    width: 328px;
    margin-top: 29px;
  }
}

.social-item-hover {
  position: relative;
  display: inline-block;
  color: #fdfdfd;
  overflow: hidden;
  background: linear-gradient(90deg, #0098EE -7.51%, #BD00FF 50%, #fdfdfd 50%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-size: 200% 100%;
  background-position: 100%;
  transition: background-position 0.5s ease;

  &:hover {
    background-position: 0 100%;
  }
  

}
.social-item a{
  display: block;
  font-family: "Satoshi-Bold", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 140%;
  color: @background_page;
  text-transform: uppercase;
  transition: .3s linear;




  @media (max-width: @desktop-width) {
    font-size: 16px;
    line-height: 140%;
  }


  @media (max-width: @mobile-width) {
    margin-top: 20px;
  }
}
.footer-privacy {
  position: relative;
  width: 1300px;
  height: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;

  @media (max-width: @desktop-width) {
    width: 1060px;
  }

  @media (max-width: @tablet-width) {
    width: 700px;
  }

  @media (max-width: @mobile-width) {
    width: 328px;
  }
}
.privacy-list {
  width: 307px;
  display: flex;
  justify-content: space-between;

  @media (max-width: @desktop-width) {
    width: 265px;
  }

  @media (max-width: @tablet-width) {
    width: 241px;
  }

  @media (max-width: @mobile-width) {
    width: 180px;
  }
}
.privacy-item,
.privacy-year {
  font-family: "Satoshi-Bold", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 140%;
  text-transform: uppercase;
  transition: 1s;



  @media (max-width: @desktop-width) {
    font-size: 16px;
    line-height: 140%;
  }

  @media (max-width: @tablet-width) {
    font-size: 14px;
    line-height: 140%;
  }

  @media (max-width: @mobile-width) {
    font-size: 14px;
    line-height: 140%;
  }
}

.privacy-item a {
  display: block;
  position: relative;
  transition: 1s;

  &:after {
    display: block;
    position: absolute;
    left: 0; /*изменить на right:0;, чтобы изменить направление подчёркивания */
    width: 0;/*задаём длинну линии до наведения курсора*/
    height: 2px; /*задаём ширину линии*/
    background-color:#28C0E2; /*задаём цвет линии*/
    content: "";
    transition: width 0.3s ease-out; /*задаём время анимации*/
  }

  &:hover:after,
  &:focus:after {
	width: 100%; /*устанавливаем значение 100% чтобы ссылка подчёркивалась полностью*/
  }
  &:hover {
    color:#28C0E2;
  }
}
.footer-arrow-privacy {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 58px;
  height: 28px;
  position: absolute;
  right: -45px;
  top: -108px;
  font-family: "Satoshi-Regular", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 140%;
  
  transform: rotate(90deg);
  writing-mode: vertical lr;
  text-orientation: upright;

  &:hover {
    animation-name: footer-arrow-privacy;
    animation-duration: 2s;
    animation-timing-function: linear;
    animation-iteration-count: 1;
    animation-direction: normal;
    animation-fill-mode: forwards;
  
  
    @keyframes footer-arrow-privacy{
      0%{
        top: -108px;
      }

      25%{
        top: 120px;
      }

      75%{
        top: 120px;
      }
    
      100%{
        top: -108px;
      }
    }
  }

  @media (max-width: @desktop-width) {
    font-size: 16px;
    line-height: 140%;
    right: -40px;
    top: -73px;
  }

  @media (max-width: @tablet-width) {
    font-size: 16px;
    line-height: 140%;
    right: -40px;
    top: -55px;
  }

  @media (max-width: @mobile-width) {
    display: none;
  }
}
.footer-arrow {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 58px;
  height: 28px;
  position: absolute;
  right: -95px;
  top: -75px;
  font-family: "Satoshi-Regular", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 140%;
  transform: rotate(90deg);
  color: @background_page;

    &:hover {
      animation-name: footer-arrow;
      animation-duration: 0.75s;
      // animation-timing-function: linear;
      // animation-iteration-count: 1;
      // animation-direction: normal;
      // animation-fill-mode: forwards;
    
    
      @keyframes footer-arrow{
        0%{
            width: 58px;
            
        }
  
        25%{
            width: 68px;
            right: -100px;
              top: -80px;
            
        }
  
        75%{
            width: 68px;
            right: -100px;
              top: -80px;
            
        }
      
        100%{
            width: 58px;
            
        }
      }
    }

  @media (max-width: @desktop-width) {
    font-size: 16px;
    line-height: 140%;
    right: -70px;
    top: -70px;

    &:hover {
      animation-name: footer-arrow-desc;
      animation-duration: .75s;
      // animation-timing-function: linear;
      // animation-iteration-count: 1;
      // animation-direction: normal;
      // animation-fill-mode: forwards;
    
    
      @keyframes footer-arrow-desc{
        0%{
            width: 58px;
            
        }
  
        25%{
            width: 68px;
            right: -75px;
            top: -75px;
            
        }
  
        75%{
            width: 68px;
            right: -75px;
            top: -75px;
            
        }
      
        100%{
            width: 58px;
            
        }
      }
    }
  }

  @media (max-width: @tablet-width) {
    right: -40px;
  }

  @media (max-width: @mobile-width) {
    top: 28px;
    right: -9px;
  }
}

