.header-arrow-service {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 80px;
  height: 28px;
  position: absolute;
  z-index: 2;
  right: -104px;
  bottom: -205px;
  font-family: "Satoshi-Regular", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 140%;
  transform: rotate(90deg);

  &:hover {

    animation-name: header-arrow-service;
    animation-duration: .75s;
    animation-timing-function: linear;
    animation-iteration-count: 1;
    animation-direction: normal;
    animation-fill-mode: forwards;
  
  
    @keyframes header-arrow-service{
      0%{
        width: 80px;
      }

      25%{
        width: 90px;
        right: -109px;
        bottom: -210px;
      }

      75%{
        width: 90px;
        right: -109px;
        bottom: -210px;
      }
    
      100%{
        width: 80px;
      }
    }
  }

  
  @media (max-width: @desktop-width) {
    font-size: 16px;
    line-height: 140%;
    bottom: -150px;

    &:hover {

      animation-name: header-arrow-service-desc;
      animation-duration: .75s;
      animation-timing-function: linear;
      animation-iteration-count: 1;
      animation-direction: normal;
      animation-fill-mode: forwards;
    
    
      @keyframes header-arrow-service-desc{
        0%{
          width: 80px;
        }
  
        25%{
          width: 90px;
          right: -109px;
          bottom: -156px;
        }
  
        75%{
          width: 90px;
          right: -109px;
          bottom: -156px;
        }
      
        100%{
          width: 80px;
        }
      }
    }
  }

  @media (max-width: @tablet-width) {
    display: none;
  }
}
