.ddt-faq-content:after {
  display: block;
  position: absolute;
  top: -2px;
  left: 0; /*изменить на right:0;, чтобы изменить направление подчёркивания */
  width: 0;/*задаём длинну линии до наведения курсора*/
  height: 2px; /*задаём ширину линии*/
  background-color:#28C0E2; /*задаём цвет линии*/
  content: "";
  transition: width 1s ease-out; /*задаём время анимации*/
}

