.case-rainbow-preview {
  width: 1300px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;

  @media (max-width: @desktop-width) {
    width: 1060px;
  }

  @media (max-width: @tablet-width) {
    width: 700px;
  }

  @media (max-width: @mobile-width) {
    display: block;
    width: 328px;
  }
}
.case-rainbow-texts {
  width: 750px;
  height: 238px;
  margin: 150px 0;

  @media (max-width: @desktop-width) {
    width: 750px;
    height: 232px;
    margin: 100px 0;
  }

  @media (max-width: @tablet-width) {
    width: 560px;
    height: 190px;
    margin: 70px 0 71px;
  }

  @media (max-width: @mobile-width) {
    width: 328px;
    height: 225px;
    margin: 50px 0 51px;
  }
}
.case-rainbow-subtitle {
  font-family: "Satoshi-Bold", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 140%;
  text-transform: uppercase;
  color: @basic-blue;

  @media (max-width: @desktop-width) {
    font-size: 16px;
    line-height: 140%;
  }

  // @media (max-width: @tablet-width) {
  //   width: 700px;
  // }

  // @media (max-width: @mobile-width) {
  //   width: 328px;
  // }
}
.case-rainbow-title {
  margin-top: 20px;
  font-family: "Satoshi-Black", sans-serif;
  font-style: normal;
  font-weight: 900;
  font-size: 70px;
  line-height: 95px;

  @media (max-width: @desktop-width) {
    
  }

  @media (max-width: @tablet-width) {
    font-size: 55px;
    line-height: 74px;
    
  }

  @media (max-width: @mobile-width) {
    font-size: 45px;
    line-height: 61px;
    margin-bottom: 30px;
  }
}
.case-rainbow-banner {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 600px;
  margin: 0 auto;
  background-color: @rainbow-bac;

  @media (max-width: @desktop-width) {
    height: 500px;
  }

  @media (max-width: @tablet-width) {
    width: 100%;
    height: 400px;
  }

  @media (max-width: @mobile-width) {
    width: 100%;
    height: 300px;
  }
}
.case-rainbow-banner-logo {
  @media (max-width: @mobile-width) {

    height: 110px;
  }
}
.case-rainbow-description {
  width: 1106px;
  height: 214px;
  margin: 200px auto 200px;
  display: flex;

  @media (max-width: @desktop-width) {
    margin: 150px auto;
    width: 919px;
    height: 192px;
  }

  @media (max-width: @tablet-width) {
    flex-wrap: wrap;
    margin: 100px auto;
    width: 520px;
    height: 379px;
  }

  @media (max-width: @mobile-width) {
    width: 328px;
    height: auto;
    margin: 70px auto 58px;
  }
}

.case-rainbow-description-column1 {

  @media (max-width: @desktop-width) {

  }

  @media (max-width: @tablet-width) {
    width: 213px;
  }

  @media (max-width: @mobile-width) {
    width: 115px;
  }
}
.case-rainbow-description-column2 {
  margin-left: 141px;

  @media (max-width: @desktop-width) {
    margin-left: 122px;
  }
  
  @media (max-width: @tablet-width) {
    margin-left: 60px;
  }

  @media (max-width: @mobile-width) {
    margin-left: 60px;
  }
}
