.header-arrow {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 80px;
  height: 28px;
  position: absolute;
  z-index: 2;
  right: -104px;
  bottom: -395px;
  font-family: "Satoshi-Regular", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 140%;
  transform: rotate(90deg);
}
.header-arrow:hover {
  animation-name: header-arrow;
  animation-duration: 0.75s;
  animation-timing-function: linear;
  animation-iteration-count: 1;
  animation-direction: normal;
  animation-fill-mode: forwards;
}
@keyframes header-arrow {
  0% {
    width: 80px;
  }
  25% {
    width: 95px;
    right: -111.5px;
    bottom: -403px;
  }
  75% {
    width: 95px;
    bottom: -403px;
    right: -111.5px;
  }
  100% {
    width: 80px;
  }
}
@media (max-width: 1919px) {
  .header-arrow {
    width: 70px;
    font-size: 16px;
    line-height: 140%;
    position: absolute;
    right: -75px;
    bottom: -320px;
  }
  .header-arrow:hover {
    animation-name: header-arrow-desc;
    animation-duration: 0.73s;
    animation-timing-function: linear;
    animation-iteration-count: 1;
    animation-direction: normal;
    animation-fill-mode: forwards;
  }
  @keyframes header-arrow-desc {
    0% {
      width: 70px;
    }
    25% {
      width: 85px;
      right: -82.5px;
      bottom: -328px;
    }
    75% {
      width: 85px;
      bottom: -328px;
      right: -82.5px;
    }
    100% {
      width: 70px;
    }
  }
}
