.header-arrow-careers {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 80px;
  height: 28px;
  position: absolute;
  z-index: 2;
  right: -104px;
  bottom: -205px;
  font-family: "Satoshi-Regular", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 140%;
  transform: rotate(90deg);

  &:hover {

    animation-name: header-arrow-careers;
    animation-duration: 0.75s;
    animation-timing-function: linear;
    animation-iteration-count: 1;
    animation-direction: normal;
    animation-fill-mode: forwards;
  
  
    @keyframes header-arrow-careers{
      0%{
        width: 80px;
      }

      25%{
        width: 90px;
        right: -109px;
        bottom: -210px;
      }

      75%{
        width: 90px;
        right: -109px;
        bottom: -210px;
      }
    
      100%{
        width: 80px;
      }
    }
  }



  @media (max-width: @desktop-width) {
    width: 70px;
    font-size: 16px;
    line-height: 140%;
    position: absolute;
    right: -75px;
    bottom: -150px;

    &:hover {

      animation-name: header-arrow-careers-desc;
      animation-duration: .75s;
      animation-timing-function: linear;
      animation-iteration-count: 1;
      animation-direction: normal;
      animation-fill-mode: forwards;

      @keyframes header-arrow-careers-desc{
        0%{
          width: 70px;
        }
  
        25%{
          width: 80px;
          right: -80px;
    bottom: -155px;
        }
  
        75%{
          width: 80px;
          right: -80px;
    bottom: -155px;
        }
      
        100%{
          width: 70px;
        }
      }
    }


  } 

  @media (max-width: @tablet-width) {
    display: none;
  }
}
