.careers-pros-card {
  position: relative;
  width: 310px;
  height: 300px;
  margin-bottom: 20px;
  background-color: @basic_white;
  overflow: hidden;

  &::after {
    display: block;
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    width: 420px;
    height: 2px;
    background-color: #303030;
  }

  @media (max-width: @desktop-width) {
    margin-top: 1px;
    width: 250px;
    height: 250px;
  }

  @media (max-width: @tablet-width) {
    width: 340px;
    height: 200px;
  }

  @media (max-width: @mobile-width) {
    margin-top: 0;
    width: 328px;
    height: 200px;
  }
}
.careers-pros-card-none-border {
  border: none;
