.case-tailors-preview {
  width: 1300px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;

  @media (max-width: @desktop-width) {
    width: 1060px;
  }

  @media (max-width: @tablet-width) {
    width: 700px;
  }

  @media (max-width: @mobile-width) {
    display: block;
    width: 328px;
    margin-top: 50px;
  }
}
.case-tailors-texts {
  width: 750px;
  height: 238px;
  margin: 150px 0;

  @media (max-width: @desktop-width) {
    width: 610px;
    height: 232px;
    margin: 100px 0;
  }

  @media (max-width: @tablet-width) {
    width: 430px;
    height: 190px;
    margin: 70px 0 70px;
  }

  @media (max-width: @mobile-width) {
    width: 328px;
    height: 225px;
    margin: 50px 0 -12px;
  }
}
.case-tailors-subtitle {
  font-family: "Satoshi-Bold", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 140%;
  text-transform: uppercase;
  color: @basic-blue;

  @media (max-width: @desktop-width) {
    font-size: 16px;
    line-height: 140%;
  }

  // @media (max-width: @tablet-width) {
  //   
  // }

  // @media (max-width: @mobile-width) {
  //   
  // }
}
.case-tailors-title {
  margin-top: 20px;
  font-family: "Satoshi-Black", sans-serif;
  font-style: normal;
  font-weight: 900;
  font-size: 70px;
  line-height: 95px;

  @media (max-width: @desktop-width) {
    
  }

  @media (max-width: @tablet-width) {
    font-size: 55px;
    line-height: 74px;
    
  }

  @media (max-width: @mobile-width) {
    font-size: 45px;
    line-height: 61px;
    margin-bottom: 30px;
  }
}
.case-tailors-banner {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 600px;
  margin: 0 auto;
  background-color: @tailors-bac;

  @media (max-width: @desktop-width) {
    width: 100%;
    height: 500px;
  }

  @media (max-width: @tablet-width) {
    width: 100%;
    height: 400px;
  }

  @media (max-width: @mobile-width) {
    width: 100%;
    height: 300px;
  }
}
.case-tailors-banner-logo {

  @media (max-width: @mobile-width) {
    height: 100px;
  }
}
.case-tailors-description {
  width: 1106px;
  height: 222px;
  margin: 200px auto;
  display: flex;

  @media (max-width: @desktop-width) {
    margin: 150px auto;
    width: 919px;
    height: 192px;
  }

  @media (max-width: @tablet-width) {
    flex-wrap: wrap;
    margin: 100px auto -25px;
    width: 520px;
    height: 512px;
  }

  @media (max-width: @mobile-width) {
    width: 328px;
    height: auto;
    margin: 70px auto 55px;
  }
}
.case-tailors-description-column1 {

  @media (max-width: @tablet-width) {
    width: 213px;
  }

  @media (max-width: @mobile-width) {
    width: 115px;
  }
}
.case-tailors-description-column2 {
  margin-left: 103px;

  @media (max-width: @desktop-width) {
    margin-left: 91px;
  }
  
  @media (max-width: @tablet-width) {
    margin-left: 56px;
  }

  @media (max-width: @mobile-width) {
    margin-left: 56px;
  }
}
.case-tailors-description-column3 {
  margin-left: 129px;

  @media (max-width: @desktop-width) {
    margin-left: 102px;
  }
  
  @media (max-width: @tablet-width) {
    margin-top: -105px;
    margin-left: 0;
  }

  @media (max-width: @mobile-width) {
    position: relative;
    margin-top: 0;
    margin-left: 0;
  }
}
.case-tailors-description-column3-mobile {

  @media (max-width: @mobile-width) {
    position: absolute;
    right: -70px;
    top: 35px;
  }
}
.case-tailors-description-column4 {
  margin-left: 133px;

  @media (max-width: @desktop-width) {
    margin-left: 90px;
  }
  
  @media (max-width: @tablet-width) {
    margin-top: -105px;
    margin-left: 107px;
  }

  @media (max-width: @mobile-width) {
    margin-top: 22px;
    margin-left: 0;
  }
}
.case-tailors-description-title {
  font-family: "Satoshi-Regular", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 140%;
  text-transform: uppercase;
  color: @basic-grey;
  margin-bottom: 20px;

  @media (max-width: @desktop-width) {
