.dropdown {
  display: none;


  @media (max-width: @mobile-width) {
    display:block;
    position: relative;
    width: 328px;
    height: 50px;
  }

}

.dropdown__button {
	position: relative;
	display: block;
	width: 100%;
	text-align: left;

	background: #FdFdFd;
	// border: 1px solid #7E9BBD;
	border-radius: 6px;
	height: 46px;

	padding-top: 13px;
	padding-bottom: 13px;
	// padding-left: 20px;
	padding-right: 35px;

	  font-family: "Satoshi-Medium", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 19px;
  text-transform: uppercase;
  border: none;

	cursor: pointer;
}

// .dropdown__button:focus, .dropdown__button--active {
// 	outline: none;
// 	box-shadow: 0px 0px 0px 4px rgba(176, 198, 225, 0.6);
// }

.dropdown__button::after {
	content: "";
	position: absolute;
	top: 50%;
	right: 15px;
	transform: translateY(-50%);
  width: 14px;
  height: 9px;
  background-image: url("../img/blog-dropdown.svg");
  
	// /* Рисуем треугольник */
  //   width: 0;
  //   height: 0;
  //   border-width: 10.4px 6px 0 6px;
  //   border-color: #79849B transparent transparent transparent;
	// border-style: solid;

	/* Снимаем клик с треугольника */
	pointer-events: none;
}


.dropdown__list {
	display: none;

	position: absolute;
	left: 0;
	top: 52px;

	margin: 0;
	padding: 0;
	list-style-type: none;

	background: #FdFdFd;
	// box-shadow: 0px 4px 8px rgba(176, 198, 225, 0.6);
	overflow: hidden;

	border-radius: 6px;
	width: 700px;

	z-index: 1;
}

.dropdown__list--visible {
	display: block;
}

.dropdown__list-item {
	margin: 0;
	padding: 0;
  font-family: "Satoshi-Medium", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 19px;
  text-transform: uppercase;
  border: none;
	padding: 10px 0px;
	cursor: pointer;
}

.dropdown__list-item:first-child {
	border-radius: 6px 6px 0 0;
}

.dropdown__list-item:last-child {
	border-radius: 0 0 6px 6px;
	border-bottom: 1px solid #7E9BBD;
}

.dropdown__list-item:hover {
	background: rgba(176, 198, 225, 0.26);
}

.dropdown__input-hidden {
	display: none;
}
