
.portfolio-content {
  width: 1300px;
  height: 230px;
  margin: 70px auto;
  display: flex;
  justify-content: space-between;
  align-items: center;

  @media (max-width: @desktop-width) {
    width: 1060px;
    height: 176px;
  }

  @media (max-width: @tablet-width) {
    display: block;
    text-align: left;
    width: 700px;
    height: auto;
  }

  @media (max-width: @mobile-width) {
    width: 328px;
    margin: 30px auto;
  }
}
.portfolio-title {

  font-family: "Satoshi-Black", sans-serif;
  font-style: normal;
  font-weight: 900;
  font-size: 170px;
  line-height: 230px;

  @media (max-width: @desktop-width) {
    font-size: 130px;
    line-height: 176px;
  }

  @media (max-width: @tablet-width) {
    font-size: 100px;
    line-height: 135px
  }

  @media (max-width: @mobile-width) {
    font-size: 50px;
    line-height: 68px;
  }
}
.portfolio-text {
  margin-top: 50px;
  width: 280px;
  font-family: "Satoshi-Bold", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 140%;
  text-align: right;
  text-transform: uppercase;

  @media (max-width: @desktop-width) {
    width: 220px;
    font-size: 16px;
    line-height: 140%;
    margin-top: 40px;
  }

  @media (max-width: @tablet-width) {
    text-align: left;
    width: 310px;
    height: 44px;
    margin-top: 20px;
  }

  @media (max-width: @mobile-width) {
    width: 328px;
    height: 44px;
  }
}
.portfolio-case {
  width: 1300px;
  height: 1003px;
  margin: 0 auto;
  margin-bottom: -246px;
  display: flex;
  border-top: 1px solid @basic-grey;

  @media (max-width: @desktop-width) {
    width: 1060px;
    height: 950px;
  }

  @media (max-width: @tablet-width) {
    width: 700px;
    height: 903px;
  }

  @media (max-width: @mobile-width) {
    display: block;
    width: 328px;
    height: 1900px;
    margin-top: 50px;
  }
}
.portfolio-case-desc {
  position: relative;
  width: 445px;
  height: 960px;
  border-right: 1px solid @basic-grey;

  @media (max-width: @desktop-width) {
    width: 340px;
    height: 860px;
  }

  @media (max-width: @tablet-width) {
    width: 249px;
    height: 760px;
  }

  @media (max-width: @mobile-width) {
    width: 328px;
    height: auto;
    border: none;
  }
}
.portfolio-case-desc-title {
  margin-top: 100px;
  font-family: "Satoshi-Bold", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 50px;
  line-height: 68px;

  @media (max-width: @desktop-width) {
  }

  @media (max-width: @tablet-width) {
    font-size: 36px;
    line-height: 49px;
    margin-top: 70px;
  }

  @media (max-width: @mobile-width) {
    margin-top: 50px;
  }
}
.portfolio-case-desc-text {
  width: 309px;
  height: 140px;
  margin-top: 30px;
  font-family: "Satoshi-Regular", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 140%;

  @media (max-width: @desktop-width) {
    width: 300px;
    font-size: 16px;
    line-height: 140%;
  }

  @media (max-width: @tablet-width) {
    margin-top: 20px;
    width: 229px;
  }

  @media (max-width: @mobile-width) {
    width: 328px;
  }
}
.portfolio-case-desc-button {
  position: absolute;
  left: 0;
  top: 390px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 120px;
  height: 120px;
  border-radius: 50%;
  border: none;
  background-color: @basic-blue;
  font-family: "Satoshi-Regular", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 140%;
  transition: 1s;

  &:hover {
    left: -15px;
    top: 375px;
    width: 150px;
    height: 150px;
  }

  &:active {
    left: 0;
    top: 390px;
    width: 120px;
    height: 120px;
  }

  @media (max-width: @desktop-width) {
    margin-top: -2px;
  }

  // @media (max-width: @tablet-width) {
  //   width: 700px;
  // }

  @media (max-width: @mobile-width) {
    margin-top: -55px;
  }
}
.portfolio-case-preview{
  overflow: hidden;
  width: 859px;
  height: 900px;

  @media (max-width: @desktop-width) {
    width: 719px;
    height: 860px;
  }

  @media (max-width: @tablet-width) {
    width: 455px;
    height: 760px;  
  }

  @media (max-width: @mobile-width) {
    overflow: visible;
    width: 328px;
    height: auto;
  }
}
.portfolio-case-wrapper {
  display: block;
  margin-top: 100px;

  &:hover {
    cursor: url(../img/portfolio-card-coursor.png), pointer;
  }

  @media (max-width: @desktop-width) {
  }

  @media (max-width: @tablet-width) {
    margin-top: 70px;
    margin-left: 3px;
  }

  @media (max-width: @mobile-width) {
    margin-bottom: -45px;
  }
}
.portfolio-case-iner {
  position: relative;
  width: 746px;
  height: 60px;
  display: flex;
  justify-content: space-between;
  margin-left: 109px;
  border-bottom: 2px solid @basic_dark;

  @media (max-width: @desktop-width) {
    height: 610px;
    height: 60px;
    justify-content:left;
  }

  @media (max-width: @tablet-width) {
    width: 430px;
    height: 49px;
    margin-left: 20px;
  }

  @media (max-width: @mobile-width) {
    width: 328px;
    height: 45px;
    margin-left: -4px;
    margin-top: -2px;
  }
}
.portfolio-case-year {
  margin-top: 18px;
  font-family: "Satoshi-Regular", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 140%;

  @media (max-width: @desktop-width) {
    font-size: 16px;
    line-height: 140%;
    margin-top: 23px;
  }

  @media (max-width: @tablet-width) {
    margin-top: 10px;
  }

  @media (max-width: @mobile-width) {
    margin-top: 8px;
  }
}
.portfolio-case-title {
  margin-top: 1px;
  margin-left: -13px;
  font-family: "Satoshi-Bold", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 36px;
  line-height: 49px;

  @media (max-width: @desktop-width) {
    margin-left: 35px;
  }

  @media (max-width: @tablet-width) {
    font-size: 24px;
    line-height: 32px;
    margin-top: 0;
    margin-left: 200px;
  }

  @media (max-width: @mobile-width) {
    margin-top: -2px;
    margin-left: 98px;
  }
}
.portfolio-case-title-saqure {

  @media (max-width: @mobile-width) {
    margin-left: 111px;
  }
}
.portfolio-case-industries {
  margin-top: 18px;
  width: 217px;
  display: flex;
  justify-content: space-between;
  font-family: "Satoshi-Bold", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 140%;

  @media (max-width: @desktop-width) {
    width: 186px;
    margin-top: 22px;
    margin-left: 84px;
    font-size: 16px;
    line-height: 140%;
  }

  @media (max-width: @tablet-width) {
    position: absolute;
    right: 0;
    bottom: -374px;
    width: 156px;
  }

  @media (max-width: @mobile-width) {

  }
}
.portfolio-case-industries-right {
  @media (max-width: @mobile-width) {
    justify-content: right;
  }
}
.portfolio-case-information {
  position: relative;
  margin-top: 50px;
  width: 750px;
  height: 600px;
  margin-left: 109px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  @media (max-width: @desktop-width) {
    width: 610px;
  }

  @media (max-width: @tablet-width) {
    display: block;
    width: 430px;
    margin-top: 30px;
    margin-left: 20px;
  }

  @media (max-width: @mobile-width) {
    width: 328px;
    margin-left: -4px;
  }
}
.portfolio-case-banner {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 420px;
  height: 600px;
  border-radius: 12px;

  @media (max-width: @desktop-width) {
    width: 340px;
    height: 500px;
  }

  @media (max-width: @tablet-width) {
    margin-bottom: 59px;
    width: 430px;
    height: 300px;
  }

  @media (max-width: @mobile-width) {
    width: 328px;
    height: 300px;
  }
}
.portfolio-case-banner-logo {

  @media (max-width: @tablet-width) {
    width: 400px;
    height: 105px;
  }
}
.portfolio-case-text {
  margin-bottom: 18px;
  width: 310px;
  font-family: "Satoshi-Regular", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 140%;

  @media (max-width: @desktop-width) {
    width: 250px;
    margin-bottom: 15px;
    font-size: 16px;
    line-height: 140%;
  }

  @media (max-width: @tablet-width) {
    margin-bottom: 14px;
    width: 430px;
  }

  @media (max-width: @mobile-width) {
    width: 328px;
  }
}
.portfolio-case-name {
  position: absolute;
  right: 4px;
  bottom: 0;
  font-family: "Satoshi-Bold", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 140%;
  color: @basic-blue;
  text-transform: uppercase;

  @media (max-width: @desktop-width) {
    right: 1px;
    bottom: 100px;
    font-size: 16px;
    line-height: 140%;
  }

  @media (max-width: @tablet-width) {
    position: absolute;
    left: 0;
    bottom: 258px;
  }

  // @media (max-width: @mobile-width) {
  //   width: 328px;
  // }
}
