.linear-hover-line {
    display: none;
    
    // transition-duration: 12s;
    // transition-property: all;
    // transition-delay: 11s;
    // transition-timing-function: linear;
  }
  
  .linear-hover:hover .linear-hover-line{
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 99999;
    width: 420px;
    height: 2px;
    background: linear-gradient(89.61deg, #28C0E2 -4.38%, #212121 106.96%);
  
  
    animation-name: border-linear;
    animation-duration: .3s;
    animation-timing-function: linear;
    animation-iteration-count: 1;
    animation-direction: normal;
    animation-fill-mode: forwards;
  }
  
  @keyframes border-linear{
    0%{
      left: -420px;
    }
  
    100%{
      left: 0;
    }
  }

  



  .linear-hover-line-last {
    display: none;
    
    // transition-duration: 12s;
    // transition-property: all;
    // transition-delay: 11s;
    // transition-timing-function: linear;
  }
  
  .linear-hover-last:hover .linear-hover-line-last{
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
    width: 860px;
    height: 2px;
    background: linear-gradient(89.61deg, #28C0E2 -4.38%, #212121 106.96%);
  
  
    animation-name: border-linear-last;
    animation-duration: 1s;
    animation-timing-function: linear;
    animation-iteration-count: 1;
    animation-direction: normal;
    animation-fill-mode: forwards;
  }
  
  @keyframes border-linear-last{
    0%{
      left: -860px;
    }
  
    100%{
      left: 0;
    }
  }