.error {
  width: 1920px;
  height: 930px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;

  @media (max-width: @desktop-width) {
    margin-top: -25px;
    width: 1060px;
    height: 618px;
  }

  @media (max-width: @tablet-width) {
    width: 700px;
    height: 874px;
  }

  @media (max-width: @mobile-width) {
    width: 328px;
    height: 378px;
  }
}
.error-number {
  width: 346px;
  height: 230px;
  margin: 0 auto;
  font-family: "Satoshi-Black", sans-serif;
  font-style: normal;
  font-weight: 900;
  font-size: 170px;
  line-height: 230px;

  @media (max-width: @desktop-width) {
    width: 265px;
    height: 176px;
    font-size: 130px;
    line-height: 176px;
  }

  @media (max-width: @tablet-width) {
    width: 204px;
    height: 135px;
    font-size: 100px;
    line-height: 135px;
  }

  @media (max-width: @mobile-width) {
    width: 102px;
    height: 68px;
    font-size: 50px;
    line-height: 68px;
    margin-top: -83px;
    margin-bottom: 10px;
  }
}
.error-title {
  width: 771px;
  height: 95px;
  font-family: "Satoshi-Black", sans-serif;
  font-style: normal;
  font-weight: 900;
  font-size: 70px;
  line-height: 95px;

  @media (max-width: @desktop-width) {
    width: auto;
    height: 68px;
    font-size: 50px;
    line-height: 68px;
  }

  @media (max-width: @tablet-width) {
    height: 49px;
    font-size: 36px;
    line-height: 49px;
  }

  @media (max-width: @mobile-width) {
    height: 32px;
    font-size: 24px;
    line-height: 32px;
  }
}
.error-text {
  display: none;

  @media (max-width: @tablet-width) {
    display: block;
    width: auto;
    height: 22px;
    text-align: center;
    font-family: "Satoshi-Regular", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 140%;
    margin-top: 25px;
  }

  @media (max-width: @mobile-width) {
    margin-top: 20px;
  }
}